import comment from '@/components/comment/comment.vue'
import {
	parseTime
} from '@/utils/index.js'
export default {
	name: 'comment',
	components: {
		comment
	},
	props: {
		comment: {
			type: Object,
			default () {
				return {}
			}
		},
		// 默认是文章评论
		isReplay: {
			type: Boolean,
			default: false
		}
	},
	filters:{
		formatTime(time){
			return parseTime(time)
		}
	},
	data() {
		return {

		};
	},
	methods: {
		toPopup(obj) {

			// 为了区分子回复还是主回复
			if (obj.isReplay) {
				// 子回复id
				obj.replayId = obj.id;
				// 主回复id
				obj.id = this.comment.comment_id
			}
			// console.log(obj)
			// console.log(this.comment)
			// console.log(obj)
			this.$emit('goPopup', obj)
		}
	}
}
